@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Caveat:wght@400;700&family=Lobster&family=Monoton&family=Open+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Playfair+Display+SC:ital,wght@0,400;0,700;1,700&family=Playfair+Display:ital,wght@0,400;0,700;1,700&family=Roboto:ital,wght@0,400;0,700;1,400;1,700&family=Source+Sans+Pro:ital,wght@0,400;0,700;1,700&family=Work+Sans:ital,wght@0,400;0,700;1,700&display=swap');

*{
    font-family: "Roboto",sans-serif;
}

body{
    height: 100vh;
    width: 100vw;
    background-color: #2f3640;
    padding: 0;
    margin:0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    width:350px;
    padding:20px;
    background-color: #fff;
    border-radius:8px;
    box-shadow:0 4px 8px rgba(0,0,0,0.1);
}

.input-container{
    width:100%;
    display: flex;
    align-items: center;
    border:2px solid #70e6f3;
    border-radius: 10px;
    overflow: hidden;
}

.cityInput{
    flex:1;
    height: 30px;
    border:none;
    font-size: 16px;
    outline:none;
    padding: 10px;
}

.search-icon{
    padding: 10px;
    height: 30px;
    cursor: pointer;
}

.search-icon img{
    margin-top:5px;
}

.image{
    text-align: center;
    height: 50px;
    margin-top: 30px;
    margin-bottom:70px;
}

.image img{
    width: 100px;
    height: 100px;
}

.temp,.location,.country{
    margin-top: 5px;
    font-size: 35px;
    color: #333;
    text-transform: uppercase;
    text-align: center;
}
.temp{
    font-weight: 500;
}

.location{
    color:#ffbc00;
    font-weight: 400;
    font-size: 40px;
}

.country{
    font-size: 18px;
    color:#888;
    font-weight: 500;
}

.cord{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 10px;
    margin-top: 10px;
}

.cord div{
    display: flex;
    flex-direction: column;
     justify-content: center;
     align-items: center;
     padding: 10px;
}

.card div span{
    font-size: 14px;
}

.humidity-percent,.wind-percent,
.cord div span:nth-child(2){
    font-size: 18px;
    font-weight: bold;
    color: #666;
    padding-top: 5px;
}
.data-container{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
.element{
    text-align: center;
}

.icon{
    height: 50px;
    width: 50px;
}

.text{
    font-size: 12px;
    color:#888; 
}

.copyright{
    text-align: center;
    font-size: 12px;
    color:#888;
}

.copyright span{
    color: #333;
}

.city-not-found,.loading-message,.error-message{
    margin-top:10px;
    color:#888;
    font-size: 25px;
    font-weight: 400;
    text-align: center;
}